import './App.css';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
} from "react-router-dom";
import React from 'react';
import Header from "./components/header/header"
import Footer from "./components/footer/footer"
import Stock from "./components/stock/stock";

// This site has 3 pages, all of which are rendered
// dynamically in the browser (not server rendered).
//
// Although the page does not ever refresh, notice how
// React Router keeps the URL up to date as you navigate
// through the site. This preserves the browser history,
// making sure things like the back button and bookmarks
// work properly.

export default function App() {
    return (
        <Router>
            <div>
                {/* A <Switch> looks through its children <Route>s and
            renders the first one that matches the current URL. */}
                <Switch>
                    <Route path="/about">
                        <Header color="white"/>
                        <About category={"About us"} title={"How we work"} subtitle={"Ever wondered how we work? Yep? Me too."}/>
                    </Route>
                    <Route path="/privacy">
                        <Header color="white"/>
                        <Privacy category={"Regulatory"} title={"Privacy Policy"} subtitle={"This Privacy Policy is meant to help you understand what information we collect, why we collect it and how you can update, manage, export and delete your information."}/>
                    </Route>
                    <Route path="/">
                        <Header color="black"/>
                        <Home />
                    </Route>
                </Switch>
                <Footer />
            </div>
        </Router>
    );
}

function Home() {
    return <Main />;
}

