function UnorderedList(props) {
    const unorderedListItems = props.items;
    let i;
    let listItems = [];

    for (i = 0; i < unorderedListItems.length; i++) {
        if (unorderedListItems[i]) {
            console.log(i + " of titles has content: " + unorderedListItems[i]);
            listItems.push(<li key={i}>{unorderedListItems[i]}</li>);
            console.log(listItems);
        } else {
            console.log(i + " of titles is null, undefined, NaN, empty, 0, or false");
        }
    }
    return(
        // these empty brackets are called fragments, with fragments no wrapper is created in the DOM
        <ul className="col-7 margin--top--spacing-05 ul--text font-family--serif">{listItems}</ul>
    )
}

